import React from 'react';
import { useRef, useState, useEffect } from 'react';
import styles from './profile.module.css'
import { Input, Button } from "@ya.praktikum/react-developer-burger-ui-components";
import {
  fetchUpdateProfile,
  dellProfileSuccess,
  fetchUpdateToken, setProfileErr
} from "../../services/slice/auth-sclice";
import {ProfileMenu} from "../../components/profile-menu/profile-menu";
import {useAppDispatch, useAppSelector} from "../../hooks/store";
import {IInputUserRegister} from "../../services/types/data";

export const Profile = () => {
  const [input, setInput] = useState<IInputUserRegister>({
    name: '',
    email: '',
    password:''
  })
  const [change, setChange] = useState(false)

  const {user, loggedIn, updateProfileErr, updateProfileSuccess} = useAppSelector(state => state.auth);
  const dispatch = useAppDispatch();

  const onChange = (e: { target: { name: string; value: string; }; }) => {
    setInput({...input, [e.target.name]: e.target.value})
    setChange(true)
  }

  const handleSubmit = (e: { preventDefault: () => void; }) => {
    e.preventDefault()
    dispatch(fetchUpdateProfile(input))
  }
  useEffect(() => {
    if(loggedIn) {
      setInput({
        name: user.name,
        email: user.email,
        password: ''
      })
    }
  }, [user])

  useEffect(() => {
    if(updateProfileErr) {
      //      .    ,  ,  action.payload  action.error === jwt expired
      //      
      dispatch(fetchUpdateToken())
      dispatch(fetchUpdateProfile(input))
    }
  }, [updateProfileErr])

  useEffect(() => {
    if(updateProfileErr) {
      setState(true)
    }
    else if(updateProfileSuccess) {
      setState(true)
    }
  }, [user])
  //    

  const [state, setState] = useState(false)
   const onFocus = () => {
     dispatch(dellProfileSuccess(false))
   }

   return (
    <div className={styles.wrap}>
      <ProfileMenu/>
      <section className={styles.content}>
        <form className={`${styles.form} input_size_default`} onSubmit={handleSubmit}>
            <Input
              name={'name'}
              placeholder={''}
              size={'default'}
              value={input.name}
              icon={'EditIcon'}
              onChange={onChange}
              onFocus={onFocus}
            />
            <Input
              name={'email'}
              type={'email'}
              placeholder={'E-mail'}
              size={'default'}
              error={input.email === ''}
              errorText={'    '}
              value={input.email}
              icon={'EditIcon'}
              onChange={onChange}
              onFocus={onFocus}
            />

            <Input
              name={'password'}
              placeholder={'  '}
              size={'default'}
              value={input.password}
              icon={'EditIcon'}
              onChange={onChange}
              onFocus={onFocus}
            />
          {change && input.email &&
            <Button type="primary" size="medium" >
              
            </Button>
          }
          {updateProfileErr &&
            <span className={`${styles.error} text text_type_main-medium mt-2`}>!</span>
          }
          {updateProfileSuccess &&
            <span className={`text text_type_main-medium mt-2`}>  </span>
          }
        </form>
      </section>
    </div>
  );
}
